/**
 * Sets the global session UUID for the JS side of the app
 * @param uuid The hashed session UUID
 */
function setSessionUUID(uuid) {
    sessionUUID = uuid
}

/**
 * Creates a new user in the database with the session UUID
 * @returns {Promise<string>} The success state of the database operation
 */
async function createUser() {
    await set(ref(database, `users/${sessionUUID}`), {
        lastAccess: Date.now(),
        userMessages: [""],
        systemMessages: [""],
        teams: [""],
    });
}

/**
 * Updates a user's lastAccess time
 * @returns {Promise<string>} The success state of the database operation
 */
async function updateAccessTime() {
    await set(ref(database, `users/${sessionUUID}/lastAccess`), {
        lastAccess: Date.now()
    });
}

/**
 * Adds a message object to the system or user list
 * @param message The message contents
 * @param role The role to add to (system or user)
 * @returns {Promise<string>} The success state of the database operation
 */
async function addMessage(message, role) {
    const messageList = await get(ref(database, `users/${sessionUUID}/${role}Messages`))
        .then(snapshot => {
            return snapshot.val()
        })

    messageList.push(message);
    await set(ref(database, `users/${sessionUUID}/${role}Messages`), messageList);
}

/**
 * Adds a team object to the database
 * @param rawString The model response containing the team data
 * @returns {Promise<void>}
 */
async function addTeam(rawString) {
    const teamJSON = teamDataToJSON(rawString, false);

    const teamsList = await get(ref(database, `users/${sessionUUID}/teams`))
        .then(snapshot => {
            return snapshot.val()
        })

    teamsList.push(teamJSON)
    await set(ref(database, `users/${sessionUUID}/teams`), teamsList)
}

async function getTeamNames() {
    let names = [];

    const teamsList = await get(ref(database, `users/${sessionUUID}/teams`))
        .then(snapshot => {
            return snapshot.val()
        })

    teamsList.forEach((value) => {
        names += value["name"]
    })

    return names
}

/**
 * Gets a team object from the database list at the provided index
 * @param index The index of the team object to retrieve
 */
async function getTeamIndexed(index) {
    const teamsList = await get(ref(database, `users/${sessionUUID}/teams`))
        .then(snapshot => {
            return snapshot.val()
        })

    // Offset by 1 to account for dummy object
    return JSON.stringify(teamsList[index + 1])
}

/**
 * Hashes multiple browser details along with timestamp and random num to generate a unique session ID (UUID)
 * @returns {Promise<String>} The UUID hashed with SHA256
 */
async function calculateSessionUUID() {
    return await getSHA256Hash(
        navigator.userAgent + navigator.language + navigator.platform +
        (new Date().toLocaleString()) + Math.floor(Math.random() * 1000000).toString()
    );
}

function debug(str) {
    console.log(str)
}

export {
    initializeFirebase,
    createUser,
    credCallAWS,
    calculateSessionUUID,
    setSessionUUID,
    addMessage,
    addTeam,
    getTeamNames,
    getTeamIndexed,
    updateAccessTime,
    sessionUUID,

    // TODO : Debug only!
    debug,
};